import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'bug_report-dados',   templateUrl: 'bug_report_dados.component.html' }) export class Bug_reportDadosComponent implements OnInit {       displayMode: Bug_reportDadosDisplayModeEnum;     displayModeEnum = Bug_reportDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = Bug_reportDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = Bug_reportDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = Bug_reportDadosDisplayModeEnum.Edit;           break;       }     }  }  enum Bug_reportDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


